/**
 *  Loads 'settings.json'  
 *  @return {object} promise containing data from a file or error information
 */
angular
	.module('notesApp')
	.service('settingsService', settingsService);

settingsService.$inject = ['$http', '$resource', '$q'];

function settingsService($http, $resource, $q) {
	var service = {
		data: {},
		getSettings: getSettings,
		settings : {
			fireBaseHttp: "",
			pathToUserPictures: "",
			defaultUserPicture: ""
		},
		settingsResource : undefined
	};

	return service;
	
	function getSettings(){
		if (service.settingsResource === undefined){
			service.settingsResource = $q.defer();
			$http.get('settings.json').then(function(object){
					service.settings = object.data;
					service.settingsResource.resolve(service.settings);
				})
				.catch(function(){

				});
		}
		return service.settingsResource.promise;
	}

}
/**
 *  Factory for userID random generation.
 *  @return {string} format: 'XXXXXXXX-XXXXXXXX-XXXXXXXX'; X represents a letter or a cypher
 */
angular
	.module('notesApp')
	.factory("uidFactory", uidFactory);

function uidFactory() {
	return {
		getUID: function() {
			var getS4 = function() {
				return Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1);
			};
			return getS4() + getS4() + '-' + getS4() + getS4() + '-' + getS4() + getS4();
		}
	};
}
/**
 *  Controller for adding notes
 *  @author lukaskalcok@gmail.com
 */
angular
	.module('notesApp')
	.controller("addNoteController", addNoteController);

addNoteController.$inject = ['$scope','databaseService','$state','uidFactory','SweetAlert', 'authService'];

function addNoteController($scope, databaseService, $state, uidFactory, SweetAlert, authService) {

	var vm = this;

	vm.newnote = {
		title: "",
		comments: {}
	};

	vm.newcomment = "";

	/**
	*  Adds a new note. Checks whether text and title is not empty and inserts a note. 
	*  If text or title is empty user, is warned and action is canceled.
	*  New note is added with current time, title, active user id and comment (which includes text, current time and active user's id) to this note.
	*/
	vm.addNewNote = function() {
		if (vm.newnote.title !== "" && vm.newcomment !== "") {
			vm.newnote.comments["" + uidFactory.getUID() + ""] = {
				text: vm.newcomment,
				created: Date.now(),
				userID: authService.getUser().uid
			};
			vm.newnote.created = Date.now();
			vm.newnote.userID = authService.getUser().uid;
			databaseService.setNote(vm.newnote, uidFactory.getUID()).then(function() {
				SweetAlert.swal({
					title: "Your note has been saved.",
					type: "success"
				});
				$state.go('notes');
			});
		} else {
			SweetAlert.swal({
				title: "You have to fill in title and text.",
				type: "warning"
			});
		}
	};
}

/**
 *  Controller for authentication of a user
 *  @author lukaskalcok@gmail.com
 */
angular
    .module('notesApp')
    .controller("authController", authController);

authController.$inject = ['$scope','databaseService','$state','settingsService','SweetAlert','authService'];

function authController($scope, databaseService, $state, settingsService, SweetAlert, authService) {

    var vm = this;

    vm.user = authService.getUser();

    /**
     *  Sets database service based on data received from setting service.
     *  Authenticate a user to database.
     *  If initialisation of setting service fails, user is informed.
     */
    vm.loadApplication = function() {
        settingsService.getSettings().then(function(data) {
            vm.settings = data;
            authService.setLocation(vm.settings.fireBaseHttp);
            databaseService.setFirebase(authService.getFirebase());
            vm.getFireBaseAuth();
        }, function(response) {
            SweetAlert.swal({
                title: "Web service initialisation failed.",
                text: response,
                type: "error"
            });
        });
    };

    vm.loadApplication();

    /**
     *  Authenticate a user to database service.
     *  Adds callbvmks for authentication success or fail.
     */
    vm.getFireBaseAuth = function() {
        authService.getAuth().$onAuth(
            function(authData){
                if (authData){
                    if ($state.current.name === 'intro'){
                        $state.go('notes');
                    }
                    authService.loginRoutine(authData);
                } else {
                    $state.go('intro');
                    if (vm.user.authenticated) {
                        if (!vm.user.unAuthRequest) {
                            SweetAlert.swal({
                                title: "Your session expired.",
                                type: "warning"
                            });
                        } else {
                            SweetAlert.swal({
                                title: "You have been sucessfully logged out.",
                                type: "success"
                            });
                        }
                    }
                    authService.logoutRoutine();
                }
            }
        );
    };
}

